<template>
  <div>
    <v-dialog
      :value="show"
      max-width="350"
      @click:outside="$emit('close')"
    >
      <v-card>
        <v-card-title class="headline i18n">{{ $vuetify.lang.t('$vuetify.auth.title') }}</v-card-title>

        <div class="container">
          <v-tabs v-model="tab" grow color="success">
            <!-- LOGIN -->
            <v-tab color="success">{{ $vuetify.lang.t('$vuetify.auth.tabs.login') }}</v-tab>

            <!-- REGISTER -->
            <v-tab>{{ $vuetify.lang.t('$vuetify.auth.tabs.register') }}</v-tab>
          </v-tabs>

          <v-tabs-items v-model="tab">
            <!-- LOGIN -->
            <v-tab-item>
              <v-card flat>
                <v-form>
                  <v-container>
                    <v-text-field
                      color="success"
                      v-model.lazy="login.mail"
                      :rules="rules.requiredMail"
                      counter
                      :maxlength="50"
                      :label="$vuetify.lang.t('$vuetify.form.mail')"
                    />
                    <v-text-field
                      color="success"
                      v-model.lazy="login.password"
                      :rules="rules.required"
                      counter
                      :maxlength="50"
                      type="password"
                      :label="$vuetify.lang.t('$vuetify.form.password')"
                    />
                  </v-container>
                </v-form>
              </v-card>
            </v-tab-item>

            <!-- REGISTER -->
            <v-tab-item>
              <v-card flat>
                <v-form>
                  <v-container>
                    <v-text-field
                      color="success"
                      v-model.lazy="register.username"
                      :rules="rules.requiredMail"
                      counter
                      :maxlength="50"
                      :label="$vuetify.lang.t('$vuetify.form.mail')"
                    />
                    <v-text-field
                      color="success"
                      v-model.lazy="register.password"
                      :rules="rules.required"
                      counter
                      :maxlength="50"
                      type="password"
                      :label="$vuetify.lang.t('$vuetify.form.password')"
                    />
                    <v-text-field
                      color="success"
                      v-model.lazy="register.passwordRepeat"
                      :rules="rules.required"
                      counter
                      :maxlength="50"
                      type="password"
                      :label="$vuetify.lang.t('$vuetify.form.passwordRepeat')"
                    />
                  </v-container>
                </v-form>
              </v-card>
            </v-tab-item>
          </v-tabs-items>
        </div>

        <v-card-actions>
          <v-spacer></v-spacer>

          <v-btn v-if="tab === 0"
            color="green darken-1"
            text
            @click="handleLogin"
          >
            {{ $vuetify.lang.t('$vuetify.auth.btns.login') }}
          </v-btn>

          <v-btn v-if="tab === 1"
            color="green darken-1"
            text
            @click="$emit('close')"
          >
            {{ $vuetify.lang.t('$vuetify.auth.btns.register') }}
          </v-btn>

          <v-btn
            color="green darken-1"
            text
            @click="$emit('close')"
          >
            {{ $vuetify.lang.t('$vuetify.auth.btns.cancel') }}
          </v-btn>
        </v-card-actions>
      </v-card>
    </v-dialog>
  </div>
</template>

<script>
export default {
  name: 'auth-dialog',
  props: {
    show: Boolean
  },
  data: () => ({
    tab: 0,
    login: {
      mail: '',
      password: ''
    },
    register: {
      mail: '',
      password: '',
      passwordRepeat: ''
    },
    rules: {
      required: null,
      requiredMail: null
    }
  }),
  created() {
    this.rules.required = [
      v => !!v || this.$vuetify.lang.t('$vuetify.rules.required')
    ]
    this.rules.requiredMail = [
      v => !!v || this.$vuetify.lang.t('$vuetify.rules.required'),
      v => v.length <= 50 || ' ',
      v => /\S+@\S+\.\S+/.test(v) || this.$vuetify.lang.t('$vuetify.rules.invalidMail')
    ]
  },
  methods: {
    handleLogin() {
      this.$emit('close')
    }
  }
}
</script>

