    <v-toolbar dense color="black" dark class="home-toolbar">
      <v-toolbar-title>{{ $vuetify.lang.t('$vuetify.global.title') }}</v-toolbar-title>

      <v-spacer></v-spacer>

      <v-toolbar-items v-if="$vuetify.breakpoint.xsOnly">
