          <v-list-item-title><v-icon>mdi-content-save-outline</v-icon></v-list-item-title>
        </v-list-item>
        <v-divider vertical dark v-if="save"/>

        <v-menu offset-y>
          <template v-slot:activator="{ on }">
            <v-btn
              icon
              dark
              v-on="on"
            >
              <v-icon>mdi-menu</v-icon>
            </v-btn>
          </template>
          <v-list>
            <v-list-item @click="toggleAuthDialog()">
