            </v-list-item>
            <v-divider />
            <v-list-item @click="showClick('Twitter')">
              <v-list-item-icon><v-icon>mdi-twitter</v-icon></v-list-item-icon>
              <v-list-item-title>{{ $vuetify.lang.t('$vuetify.global.socialmedia.twitter') }}</v-list-item-title>
            </v-list-item>
            <v-list-item @click="showClick('Facebook')">
              <v-list-item-icon><v-icon>mdi-facebook-box</v-icon></v-list-item-icon>
              <v-list-item-title>{{ $vuetify.lang.t('$vuetify.global.socialmedia.facebook') }}</v-list-item-title>
            </v-list-item>
            <v-list-item @click="toggleRoadmapDialog()">
