<script>
import AuthDialog from '@/components/auth/AuthDialog'
import DevRoadmapDialog from '@/components/roadmap/DevRoadmapDialog'

export default {
  name: 'home-toolbar',
  props: {
    'save': Boolean
  },
  components: {
    AuthDialog,
    DevRoadmapDialog
  },
  data: () => ({
    showAuthDialog: false,
    showRoadmapDialog: false
  }),
  methods: {
    showClick() {
      //console.log(item)
    },
    async saveMove() {
      await this.$store.dispatch('updateLocalMove')

      this.$store.commit('showSnackbar', { text: this.$vuetify.lang.t('$vuetify.messages.saved'), timeout: 2500 })
    },
    toggleAuthDialog() {
      this.showAuthDialog = !this.showAuthDialog;
    },
    toggleRoadmapDialog() {
      this.showRoadmapDialog = !this.showRoadmapDialog;
    }
  }
}
</script>

<style scoped>
.home-toolbar {
  position: fixed;
  width: 100%;
  top: 0;
  z-index: 10;
}
</style>